export default {
    data () {
        return {
            searchContent: '',  
            currentDate: '',
            date: this.getDate(),
            allNum: 0,
            todayNum: 0,
            orderList: []
        }
    },
    filters: {
        //paid_state：支付状态，-1：支付失败，0：未支付，1：预支付，10：已支付
        payText (val) {
            let text = ''
            switch (val) {
                case -1:
                    text = '支付失败'
                    break;
                case 0:
                    text = '未支付'
                    break;
                case 1:
                    text = '预支付'
                    break;
                case 10:
                    text = '已支付'
                    break;            
                default:                    
                    text = '未支付'
                    break;
            }
            return text
        }
    },
    created() {
        this.getDate()
        this.getStatisticsList(true)
    },
    computed: {
        startDate() {
            return this.getDate('start');
        },
        endDate() {
            return this.getDate('end')
        }
    },
    methods: {  
        handleSearch () {
            this.getStatisticsList(false)
        },
        bindDateChange(e) {
            this.searchContent = ''
            this.date = e.target.value
            this.getStatisticsList(true)
        },
        getDate(type) {
            const date = new Date();
            let year = date.getFullYear();
            let month = date.getMonth() + 1;
            let day = date.getDate();

            if (type === 'start') {
                year = year - 5;
            } else if (type === 'end') {
                year = year + 2;
            }
            month = month > 9 ? month : '0' + month;;
            day = day > 9 ? day : '0' + day;
            let str = `${year}-${month}-${day}`
            this.currentDate = str
            return str;
        },
       async getStatisticsList (b) {
            const params  = {
                page: 1,
                pageSize: 100,
                nickname: this.searchContent,
                paid_at: this.date
            }
            const res = await this.$api.adminOrderList(params)
            if (res.code === 0) {
                this.orderList = res.data.data
                if (b) {                    
                    this.todayNum = res.data.total
                }
                this.allNum = res.order_all_num
            }
        }
    },
}
